@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hor-nav{
   font-family: 'Quicksand', sans-serif;
   border:transparent;
   background-color:  #51C3D8;
   color: white;
   padding-left: 100px;
   margin-bottom: 0px;
}
p{
	text-decoration: none;
	display: inline-block;
}
.hor-heading{
 padding-left: 10px;
 font-weight: 500;
}
.usr-img{
 float: right;
 padding-right: 50px;
 padding-left: 30px;
 width: 40px;
 height:40px;
 padding-top: 5px;
 border-radius: 50%;
}
.upg-btn{
   float: right;
   margin-top:12px;
