	margin-bottom: 0px !important;
	padding-bottom: 0px !important;
	padding-right: 0px !important;
	margin-right: 0px !important;
}
small{
	float: left;
	padding-left:0px;
	font-size: 20px;
	font-weight: lighter;
	color:#293d3d;
	padding-top: 10px;
	padding-bottom: 0px;
	margin-bottom: 0px;
}
h2,h5{
	text-decoration: none;
}
.intersets{
	overflow-wrap: break-word;
	overflow: anywhere;
	width: 250px;
	padding-top: 0px !important;
	margin-top: 0px !important;
}
.transition-slow-appear{
	margin-top: 0px !important;
	-webkit-animation: slow-appear 0.2s;
	        animation: slow-appear 0.2s;
}
@-webkit-keyframes slow-appear{
	0%{width: 20px;}
	25%{width: 30px;}
	50%{width:40px;}
	75%{width: 50px;}
	100%{width: -webkit-fit-content;width: fit-content;}
}
@keyframes slow-appear{
	0%{width: 20px;}
	25%{width: 30px;}
	50%{width:40px;}
	75%{width: 50px;}
	100%{width: -webkit-fit-content;width: -moz-fit-content;width: fit-content;}
}
