@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;1,300&display=swap');

.circle-area{
  float: right;
  border-radius: 1000px 1000px 10px 10px;
  border-style: circle;
  border:2px solid lightgray;
  width: 750px;
  height: 500px;
  padding-top: 20px;
  margin-top: 20px;
  padding-right: 0px;
  border-bottom:0px transparent;
  border-right:0px;
  border-left: 0px  transparent !important;
  box-sizing: border-box;
  display: flex;
  position:relative;
  align-items: flex-start;
  justify-content: center;
  overflow-wrap: anywhere;
  shape-outside: content-box;
  overflow: auto;
  box-shadow:0px 5px 10px  #e0ebeb inset; 
  background-color: white; 
}
.btn-circle-large{
border-radius: 50%;
 height: 90px;
 width: 95px;
 padding:5px;
 background-color: white;
 border:1px solid lightgray;
 outline: none;
 position: absolute;
 hyphens: auto;
 display: flex;
 justify-content: space-around;
 align-items: center;
 overflow-wrap: anywhere !important;
 text-align: center;
 margin: 20px !important;
 box-shadow:1px 1px 15px lightgrey;  
 transition: font-size 0.5s;
 font-family: 'Raleway', sans-serif;
 font-size: 15.5px;
 background-color: white;
 color:black;
}
.btn-circle-small{
border-radius: 50%;
 height: 50px;
 width: 50px;
 padding:5px;
 background-color: white;
 border:1px solid lightgray;
 outline: none;
 position: absolute;
 hyphens: auto;
 display: flex;
 justify-content: space-around;
 align-items: center;
 overflow-wrap: anywhere !important;
