.btn-circle-medium{
 border-radius: 50%;
 height: 70px;
 width: 70px;
 padding:5px;
 background-color: white;
 border:1px solid lightgray;
 outline: none;
 position: absolute;
 hyphens: auto;
 display: flex;
 justify-content: space-around;
 align-items: center;
 overflow-wrap: anywhere !important;
 text-align: center;
 margin: 20px !important;
 box-shadow:1px 1px 15px lightgrey;  
 transition: font-size 0.5s;
 font-family: 'Raleway', sans-serif;
 font-size: 14.5px;
 background-color: white;
 color:black;
}
.btn-circle-medium:hover{
   animation: zoom-med 0.8s;
  cursor: pointer;
  outline: none;
  color:   #4d4d00;
  box-shadow:1px 1px 15px tomato; 
}
@keyframes zoom-med{
  0%{font-size:14.5px;}
  50%{font-size: 17px;}
  100%px{font-size: 14.5px;}
}
