@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

.hor-nav{
   font-family: 'Quicksand', sans-serif;
   border:transparent;
   background-color:  #51C3D8;
   color: white;
   padding-left: 100px;
   margin-bottom: 0px;
}
p{
	text-decoration: none;
	display: inline-block;
}
.hor-heading{
 padding-left: 10px;
 font-weight: 500;
}
.usr-img{
 float: right;
 padding-right: 50px;
 padding-left: 30px;
 width: 40px;
 height:40px;
 padding-top: 5px;
 border-radius: 50%;
}
.upg-btn{
   float: right;
   margin-top:12px;
   background-color: #33d6ff;
   color: white;
   font-weight: 500;
   padding: 5px;
   border:1px solid white;
   border-radius: 5px 5px 5px 5px;
   margin-right: 5px;
}
.arrw{
	vertical-align: middle;
	font-size: 30px !important;
}
ul,li{
	list-style: none;
	margin:0px;
	padding:0px;
}
.ver-nav{
	background-color:#EEEEEE;
	width:fit-content;
	padding-top: 10px;
	margin-top: 0px;
	font-size: 13px;
	font-weight: 300;
	padding-left: 10px;
	padding-right: 10px;
	display: list-item;
	text-align: center;
	padding-bottom: 0px;
	margin-bottom: 0px;
	 height: 100%;
	 position: absolute;
}
.hor-list{
	padding: 0px !important;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	height: 100%;
}
.material-icons{
	font-size: 20px;
}
.list-item{
	display: block;
	margin-left: 0px;
	text-align: center;
    padding-left: 5px !important;
    padding-top: 15px;
    padding-bottom: 5px;
    font-size: 12px;
}
.list-item:hover{
	cursor: pointer;
}
.block{
	display: block;
}
.editor-area{
	margin-left: 200px;
	margin-top: 50px;
	width: 70%;
	height: 60vh;
	margin-bottom: 0px;
	float: left;
	border:1px solid;
	border-color:rgba(0,0,0,0.1); 
	box-shadow: 0px 0px 10px rgba(0,0,0,0.3);
	padding-top: 30px;
	padding-left: 30px;
	padding-right: 30px;
	padding-bottom: 30px;
	font-size: 20px;
	font-family: 'Quicksand', sans-serif;
	overflow: auto;
}
.util-sec{
	float: left; 
	background-color:#EEEEEE;
	width: 92%;
	margin-left: 82px;
	padding: 10px;
	border-left:1px solid white;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
}
input{
	text-decoration: none;
	border:none;
	border-radius: 5px 5px 5px 5px;
	outline: none;
	padding: 8px;
	width:100%;
}
input:hover{
	outline:none;
	cursor: pointer;
	border-radius: 5px 5px 0px 0px;
}

.wraper{
  position: relative;
  padding-left: 25px;
  margin-right: 0px;
  width: fit-content !important;
}
.down-icon{
	position: absolute;
	right: -15px !important; 
	vertical-align: middle;
	padding-top: 5px !important;
	width: fit-content;
	margin-right: 0px;
	padding-right: 2px;
	padding-left: 10px !important;
	font-weight: lighter !important; 
}
.down-icon:hover{
  cursor: pointer;
}

select,option{
	text-decoration: none;
}
.styles{
	width: 93.5%;
	background-color: white;
    position: absolute;
    border-radius: 0px 0px 5px 5px;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    font-family: 'Quicksand', sans-serif;
    font-style: underline;
    animation: list-open 1s;
    scroll-behavior: smooth;
    scrollbar-width: none; 
}
@keyframes list-open{
from{
	opacity: 0;
}
to{
	opacity: 1;
}
}
.grey-color:hover{
	background-color: #EEEEEE;
	cursor: pointer;
	padding-right: 0px !important;
	margin-right:0px;
    width:100%;
}
::-webkit-scrollbar {
    width: 0vh;
    display:none;
}
h1,h2,h3,h4,h5,h6{
	padding: 5px;
	margin:0px;
}
.superFancyBlockquote{
	border:1px solid lightgrey;
	background-color: lightgrey;
	height: fit-content;
	overflow-wrap: break-word;
}

.text-area{
	border:1px dotted lightgrey;
	resize: both;
	overflow:auto;
	width: 200px;
	height: 100px;
	padding: 5px;
	font-size: 15px;
}
pre{
	text-decoration: none;
	padding: 0px;
	margin: 0px;
}


.img{
	width: fit-content;
	padding-right: 10px;
	margin-right: 5px;
}
a{
	cursor: pointer;
}
.undo-btns{
 margin-left: 80px;
 font-size: 10px;
}
.emoji-sec{
	margin-left: 80px;
	font-size: 15px;
	padding: 0px;
}