@import url('https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,300&display=swap');

.header{
	border-bottom-width:1px;
	box-shadow: 0px 5px  #ebebe0;
	padding: 10px; 
	padding-left: 150px;
	padding-top: 15px;
}
.logo{
	font-weight: bold;
	display: inline;
	font-size: 25px;
	color: #0099ff;
}
.light-weight-font{
	font-weight: lighter;
	color: #0099ff;
}
.section{
	width: fit-content;
	float: left;
	padding-top: 30px;
	padding-left: 120px;
	font-weight: 100 !important;
	font-size: 35px;
	font-family: 'Piazzolla', serif;
	color: #008fb3;
	margin-bottom: 0px !important;
	padding-bottom: 0px !important;
	padding-right: 0px !important;
	margin-right: 0px !important;
}
small{
	float: left;
	padding-left:0px;
	font-size: 20px;
	font-weight: lighter;
	color:#293d3d;
	padding-top: 10px;
	padding-bottom: 0px;
	margin-bottom: 0px;
}
h2,h5{
	text-decoration: none;
}
.intersets{
	overflow-wrap: break-word;
	overflow: anywhere;
	width: 250px;
	padding-top: 0px !important;
	margin-top: 0px !important;
}
.transition-slow-appear{
	margin-top: 0px !important;
	animation: slow-appear 0.2s;
}
@keyframes slow-appear{
	0%{width: 20px;}
	25%{width: 30px;}
	50%{width:40px;}
	75%{width: 50px;}
	100%{width: fit-content;}
}
.nxt-btn{
	margin-top: 0px;
    padding-top: 10px;
    margin-right: 10px !important;
    padding-right:  15px;
    padding-left: 15px;
    padding-bottom: 10px;
    background-image: linear-gradient(to right,#ffc266,  #ffa31a);
    border-radius: 10px 10px 10px 10px;
    color:white;
}
.bck-btn{
	margin-left: 15px;
	margin-top: 0px;
    padding-top: 15px;
    margin-right: 10px !important;
    padding-right:  15px;
    padding-left: 15px;
    padding-bottom: 15px;
    background-color:white;
    box-shadow: 1px 1px 1px lightgrey;
    border-radius: 10px 10px 10px 10px;
    color: #0099ff;
}
.theme{
	margin-top: 0px;
	width: fit-content;
	float: left;
	display:flex;
	top:0px;
    align-self: flex-start;
    padding-top: 0px;
    padding-bottom: 0px;
    margin-bottom: 0px;
}
.theme-svg{
	display:inline-block;
	float: left;
	padding-bottom: 0px;
	margin-bottom: 0px;
	top: 0px;
}
button:hover{
    cursor: pointer !important;
}
button{
	border: none;
	outline: none;
}
.nxt-btn:hover{
	background-image: linear-gradient(white,white);
	color:#ffa31a;
	outline-color: lightgrey;
}
.bck-btn:hover{
	background-color: #0099ff;
	color: white;
	outline-color: lightgrey;
	border:1px solid lightgrey;
}

a{
	text-decoration: none;
}